.container{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(#121212, #212b46);
}

.title{
  font-size: 4em;
  color: #fff;
  animation: appearing 1.2s linear 1;

}

@keyframes appearing {
  0%{
    opacity: 0;
    transform: translateY(-100%);
  }
  100%{
    opacity: 1;
    transform: translateY(0%);
  }
}

.containerInput{
  position: relative;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px;
  margin: 34px 0;
  display: flex;
  border-radius: 8px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5) ;
}

.containerInput input{
  background: transparent;
  border: 0;
  outline: none;
  font-size: 20px;
  margin-right: 8px;
  caret-color: #f1f1f1;
  color: #f1f1f1;
}

.containerInput input::placeholder{
  color: #f1f1f1;
}

.buttonSearch{
  position: absolute;
  right: 0;
  top: 0px;
  width: 50px;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
  background-color: transparent;
  height: 54px;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .2s;
}

.buttonSearch:hover{
  background-color: black;
}

.main{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #fff;
  width: 30em;
  border-radius: 8px;
}

.main h2{
  margin: 16px 0;
  font-size: 19px;
}

.main span{
  margin-bottom: 16px;
  font-weight: bolder;
}

@media (max-width: 555px) {

  .title{
    font-size: 2em;
  }
  .main{
    width: 90vw;
  }
}